body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.chart-wrapper {
    display: flex;
    flex-direction: column;
    background: aliceblue;
    padding: 10px;
    border-radius: 5px;
    box-sizing: border-box;
    position: relative;
}

.y-scale__item {
    font-size: 12px;
    fill: lightgrey;
}

.x-scale__item {
    font-size: 12px;
    fill: lightgrey;
}
.info-card {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    flex-direction: column;
    font-size: 14px;
    padding: 10px;
    border-radius: 2px;
    box-sizing: border-box;
    background-color: #fff;
    box-shadow: 0 0 5px lightgrey;
}

.info-card__date {
    color: darkgray;
    margin: 0;
}

.info-card__currency-rate {
    display: flex;
    margin: 0;
}

.info-card__currency {
    margin: 0 2px 0 0;
}

.info-card__currency-value {
    margin: 0 10px 0 0;
}

.info-card__currency-dynamic-wrap {
    display: flex;
}

.info-card__currency-dynamic {
    margin: 0;
}

.info-card__currency-dynamic_up {
    color: green;
}

.info-card__currency-dynamic_down {
    color: red;
}
